import React from "react";
import { connect } from "react-redux";
import UsersFucn from "./UsersFunc";
import { follow, unfollow, setUsers, setCurrent, setAllUsersCount, setIsFetch, togglefollowingProgress, getUsersThunkCreator, getUnfollow, getFollow } from "../../Redux/users-reducer";
import Preloader from "../common/preloader/Preloader";
import { compose } from "redux";

import { withAuthRedicert } from "../../hoc/withAuthRedirect";
import { getUsers, getCurrentPage, getPageSize, getTotalUsersCount, getIsFetch, getFollowingProgress } from "../../Redux/users-select";

class UsersContainer extends React.Component {

    componentDidMount() {
        this.props.getUsersThunkCreator(this.props.currentPage, this.props.pageSize);


    }

    pageCurrentNumber = (PageNumber) => {
        this.props.getUsersThunkCreator(PageNumber, this.props.pageSize)

    }

    render() {
        return <>
            {
                this.props.isFetch ? <Preloader /> : null
            }
            <UsersFucn totalUsersCount={this.props.totalUsersCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                pageCurrentNumber={this.pageCurrentNumber}
                users={this.props.users}
                unfollow={this.props.unfollow}
                follow={this.props.follow}
                isFetch={this.props.isFetch}
                followingProgress={this.props.followingProgress}
                togglefollowingProgress={this.props.togglefollowingProgress}
                getUnfollow={this.props.getUnfollow}
                getFollow={this.props.getFollow}
            />
        </>
    }
}

const mapStateToProps = (state) => {
    return {

        users: getUsers(state),
        currentPage: getCurrentPage(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        isFetch: getIsFetch(state),
        followingProgress: getFollowingProgress(state)
    }
}




export default compose(
    connect(mapStateToProps, {
        follow, unfollow, setUsers,
        setCurrent, setAllUsersCount, setIsFetch,
        togglefollowingProgress, getUsersThunkCreator, getFollow, getUnfollow
    }),
    withAuthRedicert
)(UsersContainer)