import React, { useState } from "react";
import s from "../Users/Users.module.scss";

let Paginator = ({ totalUsersCount, pageSize, currentPage, pageCurrentNumber, portionSize = 10 }) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCounter = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPositionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPositionPageNumber = portionNumber * portionSize;
  
    return (

        <div className={s.paginationItem}>
            {portionNumber > 1 && <button onClick={() => { setPortionNumber(portionNumber - 1); }}>Prev</button>}

            {pages.filter(p => p >= leftPositionPageNumber && p <= rightPositionPageNumber)
                .map(p => {
                    return <span key={p} className={currentPage === p && s.currentPage} onClick={() => { pageCurrentNumber(p) }}> {p}</span>

                })}

            {portionCounter > portionNumber && <button onClick={() => { setPortionNumber(portionNumber + 1) }}>Next</button>}
        </div>

    )

}

export default Paginator