import React from "react";
import s from "./Users.module.scss";
import Paginator from "../common/Paginator";
import User from "./User";

let UsersFucn = ({ totalUsersCount, pageSize, currentPage, pageCurrentNumber, ...props }) => {

    return (


        <div className={s.usersConteiner}>
            <Paginator totalUsersCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage} pageCurrentNumber={pageCurrentNumber} />
            {
                props.users.map(u => <User users={u} followingProgress={props.followingProgress} getUnfollow={props.getUnfollow} getFollow={props.getFollow} portionSize={10} />)
            }

        </div >

    )

}

export default UsersFucn