import React from "react";
import s from "./Users.module.scss";
import photoImgPerson from "../../access/images/user.png";
import { NavLink } from "react-router-dom";
import cn from "classnames";

let User = ({ users, followingProgress, getUnfollow, getFollow }) => {

    return (
        <div key={users.id} className={s.usersBlock}>
            <div className={s.avatarUser}>
                <NavLink to={'/profile/' + users.id}>
                    <img src={users.photos.small != null ? users.photos.small : photoImgPerson} alt="" />
                </NavLink>
                {users.followed ?
                    <button disabled={followingProgress.some(id => id === users.id)}
                        onClick={() => { getUnfollow(users.id) }}>UnFollow</button> :
                    <button disabled={followingProgress.some(id => id === users.id)}
                        onClick={() => { getFollow(users.id) }}>Follow</button>}

            </div>
            <div >
                <div >Name: {users.name}</div>
                <div>Status: {users.status}</div>
                <div>City: {"users.point.city"}</div>
                <div>Country: {"users.point.country"}</div>
            </div>

        </div>)
}

export default User